.Login {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 90px;
    margin-top: 50px;
    background-color: rgba(117, 107, 107, 0.11);
  }
  
  .Login-container {
    max-width: 600px;
    height: 500px;
    background-color: rgba(117, 107, 107, 0.11);
    color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 16rem;
    margin-bottom: 50px;
  }
  
  .contact-input {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .input-container {
    position: relative;
    width: 400px;
  }
  
  .input-container input {
    width: 100%;
    height: 40px;
    padding: 10px 5px;
    box-sizing: border-box;
  }
  
  .input-container label {
    position: absolute;
    top: 50%;
    left: 10px;
    transform: translateY(-50%);
    background-color: rgba(117, 107, 107, 0.11);
    padding: 0 5px;
    transition: all 0.2s;
    pointer-events: none;
    font-size: 14px;
    color: grey;
  }
  
  .input-container input:focus + label,
  .input-container input:not(:placeholder-shown) + label {
    top: 20px;
    transform: translateY(-50%) scale(0.75);
    color: black;
    background-color: white;
  }
  
  .loginbtncontainer {
    width: fit-content;
  }
  
  .loginbtn {
    margin-top: 30px;
    border: 2px solid black;
    border-left: none;
    background-color: white;
    color: black;
    width: 25.5rem;
  }
  
  .Forgot-button {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .Forgot-button button {
    font-size: 20px;
    margin-top: 20px;
    color: red;
    background-color: rgba(117, 107, 107, 0.11);
    margin-right: 170px;
  }
  
  .form {
    position: relative;
    left: 6rem;
    margin-top: 40px;
  }
  
  .Login h1 {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 20px;
  }
  
  .lg {
    display: contents;
  }
  
  .MA {
    display: flex;
    margin-left: 30px;
  }
  