.Profile-container {
    display: flex;
    margin: 50px 0;
    background-color: rgba(117, 107, 107, 0.11);
    padding: 20px;
  }
  
  .sidebar {
    background-color: rgba(117, 107, 107, 0.11);
    width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }
  
  .profile-section-one {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .profile-image-username {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .profile-icon img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-bottom: 10px;
  }
  
  .username-container h2 {
    margin: 5px 0;
    font-size: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .second-section-of-first {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  
  .second-section-of-first p {
    cursor: pointer;
    margin: 5px 0;
    color: black;
  }
  
  .second-section-of-form {
    flex: 1;
    margin-left: 20px;
    display: flex;
    flex-direction: column;
    width: calc(100% - 270px); /* Adjusted width based on sidebar width */
  }
  
  .pi-edit {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  .pi-edit p {
    margin: 0;
    font-weight: bold;
  }
  
  .name,
  .gender-container,
  .e-n {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  
  label {
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  input {
    height: 40px;
    padding: 10px;
    box-sizing: border-box;
  }
  
  .gender {
    display: flex;
    gap: 20px;
  }
  
  .gender-row {
    display: flex;
    align-items: center;
  }
  
  .gender-row label {
    margin-right: 10px;
  }
  