.products {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    background-color: #abb8c3;
    margin-top: 20px;
}
.Allproducts img{
    width: 350px;
    height: 250px;
}

.product-container {
    display: flex;
    flex-direction: column;
    border: 2px solid;
    margin-top: 20px;
}

.Prodetails {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.Product-img {
    display: flex;
    justify-content: space-between;
}

.Product-img img {
    height: 100px;
    width: 100px;
}